.cell {
  position: relative;
  overflow: hidden;
  color: #ffffff;
  background: #ffffff;
  min-height: 394px; }
  .cell img.big {
    height: 394px;
    width: 100%;
    -o-object-fit: cover;
       object-fit: cover; }
  .cell h3.sub-header {
    font: 700 0.875em "Myriad Pro", arial;
    color: #fd634e;
    text-transform: uppercase;
    margin-top: 88.5px; }
  .cell h2 {
    font: 400 2.1875em "Open Sans", sans-serif;
    color: #a2ca28;
    text-transform: uppercase; }
  .cell p {
    font: 700 0.875em "Myriad Pro", arial;
    color: #656e74;
    line-height: 1.857; }
    .cell p > span {
      color: #a2ca28;
      text-decoration: underline;
      white-space: nowrap; }
      .cell p > span:hover {
        cursor: pointer; }
  .cell span.users {
    position: absolute;
    bottom: 3em;
    font: 400 0.75em "Myriad Pro", arial;
    color: #ffffff; }
    .cell span.users span.value {
      color: #ffffff; }
    .cell span.users img {
      margin-bottom: 5px;
      margin-left: 10px; }
    .cell span.users:hover {
      cursor: pointer;
      color: #f7614c; }
  .cell .date, .cell .tag {
    position: absolute;
    top: 3em;
    color: #ffffff;
    font: 700 0.875em "Myriad Pro", arial;
    text-transform: uppercase;
    letter-spacing: 2px; }
  .cell .date {
    left: 3em; }
  .cell .tag {
    right: 3em; }
    .cell .tag img {
      margin-left: 5px; }
  .cell .left {
    left: 3em; }
  .cell .right {
    right: 3em; }
  .cell .hidden {
    display: none; }
  .cell .person-ico, .cell .avocado-ico {
    position: absolute;
    right: 4em;
    top: 5em; }
  .cell .avocado-ico {
    display: none; }

.centered {
  text-align: center; }
  .centered .person-ico, .centered .cell .avocado-ico, .cell .centered .avocado-ico, .centered .avocado-ico {
    display: none; }

.text-left {
  text-align: left; }
  .text-left span.drop > img {
    display: none; }

.green {
  background: #a2ca28 !important; }
  .green .avocado-ico {
    display: inline-block; }
  .green .person-ico, .green .cell .avocado-ico, .cell .green .avocado-ico {
    display: none; }
  .green h3.sub-header {
    color: #ffffff; }
  .green h2 {
    color: #ffffff; }
  .green p {
    color: #ffffff; }
  .green span.big > img {
    display: none; }
